@import "https://fonts.googleapis.com/css?family=Poppins:300,400,500,600,700";


body {
    font-family: 'Poppins', sans-serif;
    background: #fafafa;
}

p {
    font-family: 'Poppins', sans-serif;
    font-size: 1.1em;
    font-weight: 300;
    line-height: 1.7em;
    color: #999;
}



/*--------------------*/
/* PAGE CONTENT STYLE */
/*--------------------*/


#content {
    width: 100%;
    padding: 20px;
    min-height: 100vh;
    -webkit-transition: all 0.3s;
    -o-transition: all 0.3s;
    transition: all 0.3s;
}

.wrapper{
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: stretch;
      align-items: stretch;
}

/*----------------*/
/* SIDEBAR STYLE */
/*----------------*/


.sidebar {
    /* don't forget to add all the previously mentioned styles here too */
    min-width:250px;
    max-width:250px;
    min-height: 100vh;    
    background: #333333;
    color: #fafafa;
    -webkit-transition: all 0.3s;
    -o-transition: all 0.3s;
    transition: all 0.3s;
}




.sidebar ul li a{
  text-align:center;
}


a[data-toggle="collapse"]{
  position:relative;
}

.dropdown-toggle::after{
  display:block;
  position: absolute;
  top:50%;
  right:20px;
  -webkit-transform: translateY(-50%);
      -ms-transform: translateY(-50%);
          transform: translateY(-50%);
}

/* Same dropdown links padding*/
.sidebar.active ul ul a {
    padding: 10px !important;
}


a, a:hover, a:focus {
    color: inherit;
    text-decoration: none;
    -webkit-transition: all 0.3s;
    -o-transition: all 0.3s;
    transition: all 0.3s;
}



.sidebar .sidebar-header {
    padding: 20px;
    background: #333333;
}

.sidebar ul.components {
    padding: 20px 0;
    
    /*
    border-bottom: 1px solid #fafafa;
    */

}

.sidebar ul p {
    color: #fff;
    padding: 10px;
}

.sidebar ul li a {
    padding: 10px;
    font-size: 1.1em;
    display: block;
}
.sidebar ul li a:hover {
    color: #333333;;
    background: white;
    
}

/*SUB MENU ACTIVE AND EXPANDED */
.sidebar ul li.active > a, a[aria-expanded="true"] {
    color: #333333;;
    background: white;
    
}

.sidebar.active .dropdowntoggle::after{
  top:auto;
  bottom:10px;
  right:50%;
  -webkit-transform: translateX(50%);
  -ms-transform: translateX(50%);
  transform: translateX(50%);
}


ul ul a {
    font-size: 0.9em !important;
    padding-left: 30px !important;
    background: #666666;
}

/*-------------------*/
/* MOBILE MEDIA QUERY*/
/*-------------------*/


@media (max-width: 768px){
    .sidebar{

      min-width: 100px;
      max-width:100px;
      text-align: center;

    }

    .circularImg{

      display: none;
    }

    .profileName{
      display:none;
    }

    /* Downsize the navigation links font size */
    .sidebar ul li a {
        padding: 20px 10px;
        font-size: 0.85em;
        text-align: center;
        word-wrap: break-word;
    }

    .sidebar ul li a i {
        margin-right:  0;
        display: block;
        font-size: 1.8em;
        margin-bottom: 5px;
    }

    /* Adjust the dropdown links padding*/
    .sidebar ul ul a {
        padding: 10px !important;
    }

    /* Changing the arrow position to bottom center position, 
      translateX(50%) works with right: 50% 
      to accurately  center the arrow */
    .dropdown-toggle::after {
        top: auto;
        bottom: 10px;
        right: 50%;
        -webkit-transform: translateX(50%);
        -ms-transform: translateX(50%);
        transform: translateX(50%);
    }

}



